//========= ============= ==================== =========

import classes from "./Products.module.css";
import Aux from "../HOC/Auxalliry";
import MainNav from "../Home/Nav/MainNav";
import Product from "./Product/Product";
import { app } from "../../base";
import { useState, useEffect } from "react";
import Spinner from "../Spinner/Spinner";
import { Icon } from "@iconify/react";

const db = app.firestore();
const ProductsPage = () => {
  const [products, setProducts] = useState(null);
  const [loaded, setLoaded] = useState(false);
  const [filters, setFilters] = useState();
  const [show, setShow] = useState({});
  let init = true;
  let style = {};

  useEffect(() => {
    const fetProducts = async () => {
      const productsCollection = await db.collection("products").get();
      setProducts(
        productsCollection.docs.map((doc) => {
          return doc.data();
        })
      );
      setLoaded(true);
    };
    fetProducts();
  }, []);
  if (filters) {
    style = {
      color: "gold",
      cursor: "pointer",
    };
  }

  return (
    <div className={classes.body}>
      {loaded ? (
        <>
          <MainNav />

          <Icon
            className="menu"
            icon="eva:menu-outline"
            onClick={() => {
              console.log("clicked");
              setShow({ display: "flex" });
            }}
          />

          <div className={classes.sideNav} style={show}>
            <Icon
              className="x"
              onClick={() => {
                console.log("clicked");
                setShow({ display: "none" });
              }}
              icon="bi:x-lg"
            />
            <div>
              <h1
                className={classes.filters}
                style={style}
                onClick={() => {
                  if (filters) {
                    setFilters(false);
                  }
                }}
              >
                Filters
              </h1>
              <div>
                <h2>Type</h2>
                <ul>
                  <li>
                    <p
                      onClick={() => {
                        setFilters({
                          filterDiamond: true,
                        });
                      }}
                    >
                      Dimond
                    </p>
                  </li>
                  <li>
                    <p
                      onClick={() => {
                        setFilters({
                          filterGold: true,
                        });
                      }}
                    >
                      Gold
                    </p>
                  </li>
                </ul>
              </div>
            </div>
            /*{" "}
            <div>
              <h2>Prices</h2>
              <ul>
                <li>
                  <p
                    onClick={() => {
                      setFilters({
                        filter400: true,
                      });
                    }}
                  >
                    From 200$ to 400$
                  </p>
                </li>
                <li>
                  <p
                    onClick={() => {
                      setFilters({
                        filter600: true,
                      });
                    }}
                  >
                    From 400$ to 600$
                  </p>
                </li>
                <li>
                  <p
                    onClick={() => {
                      setFilters({
                        filter1000: true,
                      });
                    }}
                  >
                    From 600$ to 1000$
                  </p>
                </li>
                <li>
                  <p
                    onClick={() => {
                      setFilters({
                        filterabove: true,
                      });
                    }}
                  >
                    1000$ and above..
                  </p>
                </li>
              </ul>
            </div>
          </div>
          <div className={classes.container}>
            <div className={classes.productsContainer}>
              {products.map((p) => {
                return (
                  <>
                    {init ? (
                      <Product
                        image={p.image}
                        id={p.id}
                        title={p.title}
                        price={p.price}
                        type={p.type}
                        filters={filters}
                        products={products}
                        key={p.id}
                      />
                    ) : null}
                  </>
                );
              })}
            </div>
          </div>
        </>
      ) : (
        <Spinner />
      )}
    </div>
  );
};
export default ProductsPage;
