import React, { useEffect, useState } from "react";
import { app } from "../../base";
import Product from "../../Components/Products/Product/Product";
import classes from "./UploadProducts.module.css";
const db = app.firestore();
let change = 0;
function UploadProducts() {
  const [fileUrl, setFileUrl] = useState(null);
  const [numbers, setNumbers] = useState([]);
  const [login, setLogin] = useState(false);
  const [update, setUpdate] = useState(1);
  const loginHandeler = (e) => {
    if (numbers && e.target.value === numbers[1].password) {
      setLogin(true);
    }
  };
  const onFileChange = async (e) => {
    const file = e.target.files[0];
    if (file) {
      const storageRef = app.storage().ref();
      const fileRef = storageRef.child(file.name);
      await fileRef.put(file);
      setFileUrl(await fileRef.getDownloadURL());
    }
  };

  const onSubmit = (e) => {
    setUpdate(change++);
    e.preventDefault();
    const id = "product";
    const title = e.target.title.value;
    const type = e.target.type.value;
    const price = e.target.price.value;
    // if (!title || !fileUrl) {
    //   return;
    // }
    if (numbers) {
      if (title && fileUrl && type && price) {
        db.collection("products")
          .doc(id + numbers[0].counter)
          .set({
            id: id + numbers[0].counter,
            title: title,
            image: fileUrl,
            type: type,
            price: price,
          });
        console.log("uploaded");
      }
    }
    db.collection("numbers")
      .doc("numbers")
      .update({
        counter: numbers[0].counter + 1,
      });
  };

  useEffect(() => {
    const getData = async () => {
      const Numbers = await db.collection("numbers").get();
      setNumbers(
        Numbers.docs.map((doc) => {
          return doc.data();
        })
      );
    };
    getData();
  }, [update]);

  return (
    <>
      {login ? (
        <div>
          <h1 className={classes.admin}>Admin</h1>
          <div className={classes.container}>
            <form onSubmit={onSubmit}>
              <label>Title:</label>
              <input type="text" name="title" required></input>
              <label>Type:</label>
              <select name="type" required>
                <option value="gold">Gold</option>
                <option value="diamond">Diamond</option>
              </select>
              <label>Price in USD</label>
              <input type="number" name="price" required></input>
              <label>Product Picture:</label>
              <input
                type="file"
                onChange={onFileChange}
                className={classes.file}
                required
              ></input>
              <button>Submit</button>
            </form>
            <ul> </ul>
          </div>
        </div>
      ) : (
        <form className={classes.loginForm}>
          <label>PASSWORD</label>
          <input type="password" onChange={loginHandeler}></input>
        </form>
      )}
    </>
  );
}

export default UploadProducts;
