import { useState, useEffect } from "react";
import classes from "./Product.module.css";
import { app } from "../../../base";
import { Icon } from "@iconify/react";
import Spinner from "../../Spinner/Spinner";
import ShoppingCart from "../../ShoppingCart/ShoppingCart";
const db = app.firestore();
const i = 3;
function Product({ image, id, title, price, type, filters, productsObj }) {
  const [loaded, setLoaded] = useState(false);
  const [products, setProducts] = useState(null);
  const [addedProducts, setAddedProducts] = useState(null);
  const [counter, setCounter] = useState(1);

  let clicked = false;
  let username;
  if (localStorage.getItem("user")) {
    username = JSON.parse(localStorage.getItem("user")).username;
  }

  const [backgroundImage, setBackgroundImage] = useState({
    backgroundImage: `linear-gradient(to bottom, transparent, black) , url(${image}) `,
    height: "25rem",
  });

  const func = () => {
    setCounter(counter + 1);
    if (backgroundImage.height === "25rem") {
      <span class="iconify" data-icon="icons8:buy"></span>;
      setBackgroundImage({
        height: "50rem",
        backgroundImage: `linear-gradient(to bottom, transparent, black) , url(${image}) `,
      });
    } else {
      setBackgroundImage({
        height: "25rem",
        backgroundImage: `linear-gradient(to bottom, transparent, black) , url(${image}) `,
      });
    }
  };

  const uploadProduct = () => {
    if (username) {
      if (addedProducts) {
        const temp = addedProducts.split(" ").filter((w) => w !== "");

        console.log(temp);
        let exists = false;
        temp.forEach((v) => {
          if (v === id) {
            exists = true;
          }
        });

        if (!exists) {
          db.collection("users")
            .doc(username)
            .update({
              products: addedProducts + " " + id,
            });
        }
      } else {
        db.collection("users").doc(username).update({
          products: id,
        });
      }
    } else {
      console.log("login first");
    }
  };
  useEffect(() => {
    if (username) {
      const getData = async () => {
        const productsIds = await db
          .collection("users")
          .doc(username)

          .get();
        setAddedProducts(productsIds.data().products);
      };

      //  else {
      //   productsIds.then((res) => {
      //     const respond = res.data();
      //     console.log(respond);
      //   });
      // }

      getData();
    }
  }, [counter]);
  return (
    <>
      {!filters ? (
        <div
          className={classes.product}
          style={backgroundImage}
          onClick={func}
          key={id}
        >
          {backgroundImage.height === "50rem" ? (
            <Icon icon="icons8:buy" onClick={uploadProduct} />
          ) : null}
          <p>{title}</p>
          <p>{price}$</p>
        </div>
      ) : null}
      {filters ? (
        <>
          {filters.filter400 && price >= 200 && price <= 400 ? (
            <div
              className={classes.product}
              style={backgroundImage}
              onClick={func}
              key={id}
            >
              <p>{title}</p>
              <p>{price}$</p>
            </div>
          ) : null}
          {filters.filter600 && price >= 400 && price <= 600 ? (
            <div
              className={classes.product}
              style={backgroundImage}
              onClick={func}
              key={id}
            >
              {backgroundImage.height === "50rem" ? (
                <Icon icon="icons8:buy" onClick={uploadProduct} />
              ) : null}
              <p>{title}</p>
              <p>{price}$</p>
            </div>
          ) : null}
          {filters.filter1000 && price >= 600 && price <= 1000 ? (
            <div
              className={classes.product}
              style={backgroundImage}
              onClick={func}
              key={id}
            >
              {backgroundImage.height === "50rem" ? (
                <Icon icon="icons8:buy" onClick={uploadProduct} />
              ) : null}
              <p>{title}</p>
              <p>{price}$</p>
            </div>
          ) : null}
          {filters.filterabove && price >= 1000 ? (
            <div
              className={classes.product}
              style={backgroundImage}
              onClick={func}
              key={id}
            >
              {backgroundImage.height === "50rem" ? (
                <Icon icon="icons8:buy" onClick={uploadProduct} />
              ) : null}
              <p>{title}</p>
              <p>{price}$</p>
            </div>
          ) : null}
          {filters.filterDiamond && type === "diamond" ? (
            <div
              className={classes.product}
              style={backgroundImage}
              onClick={func}
              key={id}
            >
              {backgroundImage.height === "50rem" ? (
                <Icon icon="icons8:buy" onClick={uploadProduct} />
              ) : null}
              <p>{title}</p>
              <p>{price}$</p>
            </div>
          ) : null}
          {filters.filterGold && type === "gold" ? (
            <div
              className={classes.product}
              style={backgroundImage}
              onClick={func}
              key={id}
            >
              {backgroundImage.height === "50rem" ? (
                <Icon icon="icons8:buy" onClick={uploadProduct} />
              ) : null}
              <p>{title}</p>
              <p>{price}$</p>
            </div>
          ) : null}
        </>
      ) : null}
    </>
  );
}
export default Product;
