import classes from "./Header.module.css";
import img1 from "../../../assets/imgs/piaget.png";
import img3 from "../../../assets/imgs/graff.png";
import img2 from "../../../assets/imgs/cartier.png";
function Header() {
  let i = 0;
  let goldryText = "GOLDRY";
  let arr = [];
  function typing() {
    if (i < goldryText.length) {
      arr.push(goldryText.charAt(i));
      i++;
      setTimeout(typing, 5000);
    }
  }

  return (
    <div className={classes.header}>
      <div className={classes.img}></div>
      <div className={classes.textContainer}>
        <h1 className={"headerText animate__animated animate__fadeInUp"}>
          GOLDRY
        </h1>
        <h2 className={classes.subHeaderText}>
          the wolrd biggest jewelry store
        </h2>
      </div>
      <h1 className={classes.trustedBy}>Trusted By</h1>

      <div className={classes.logos}>
        <ul>
          <li>
            <img src={img1} alt="logo1" className={classes.logo1} />
          </li>
          <li>
            <img src={img2} alt="logo1" className={classes.logo2} />
          </li>
          <li>
            <img src={img3} alt="logo1" className={classes.logo3} />
          </li>
        </ul>
      </div>
    </div>
  );
}
export default Header;
