import classes from "./Signup.module.css";
import Mainav from "../Home/Nav/MainNav";
import Footer from "../Footer/Footer";
import { app } from "../../base";
import { Icon } from "@iconify/react";
import { useState, useEffect } from "react";
import Spinner from "../Spinner/Spinner";
const SignUp = () => {
  const db = app.firestore();
  const [users, setUsers] = useState(null);
  const [login, setLogin] = useState(false);
  let display = "none";
  const [taken, setTaken] = useState(false);
  const [test, setTest] = useState(false);
  const [match, setMatch] = useState(true);
  const [short, setShort] = useState(false);
  const [created, setCreated] = useState(false);
  const [correct, setCorrect] = useState(true);
  const [isValid, setIsValid] = useState(true);
  useEffect(() => {
    const fetchUsers = async () => {
      const usersCollection = await db.collection("users").get();
      setUsers(
        usersCollection.docs.map((doc) => {
          return doc.data();
        })
      );
    };
    fetchUsers();
  }, []);

  const submitHandeler = (e) => {
    e.preventDefault();
    if (users) {
      console.log(users);
      users.map((v) => {
        if (v.username === e.target.username.value) {
          console.log("taken");
          setTaken(true);
          setTest(true);
        }
      });
      if (e.target.password.value.length < 8) {
        setShort(true);
      } else {
        setShort(false);
      }
      if (test && e.target.password.value !== e.target.confirm.value) {
        setMatch(false);
      }
      if (test && e.target.password.value === e.target.confirm.value) {
        console.log(taken);
        const user = {
          username: e.target.username.value,
          password: e.target.password.value,
        };

        localStorage.setItem("user", JSON.stringify(user));
        setMatch(true);
        db.collection("users").doc(e.target.username.value).set({
          username: e.target.username.value,
          password: e.target.password.value,
          products: "",
        });
        setCreated(true);
      }
    } else {
      <Spinner />;
    }
  };
  const valid = (e) => {
    let i = e.target.value;
    if (test) {
      setTest(false);
    }
    i = i.toLowerCase();
    i = i.replaceAll(" ", "_");
    e.target.value = i;

    if (Number(i[0])) {
      setIsValid(false);
    } else {
      setIsValid(true);
    }
  };
  const submitLogin = (e) => {
    e.preventDefault();
    if (users) {
      users.forEach((v) => {
        if (
          e.target.username.value === v.username &&
          e.target.password.value === v.password &&
          !localStorage.getItem("user")
        ) {
          setCreated(true);

          localStorage.setItem("user", JSON.stringify(v));
        }
      });
      if (!created) {
        setCorrect(false);
      }
    }
  };
  return (
    <>
      {!created ? (
        <>
          <Mainav />
          <div className={classes.imgContainer}>
            <div className={classes.signUpFormContainer}>
              {!login ? (
                <form onSubmit={submitHandeler}>
                  <label>choose your username</label>
                  <input
                    type="text"
                    name="username"
                    required
                    onChange={valid}
                    maxLength="15"
                  ></input>
                  {taken ? (
                    <p>Username is already Taken try anther one</p>
                  ) : null}
                  {!isValid ? <p>username can't begin with a number</p> : null}
                  <label>Create your password</label>
                  <input type="password" name="password" required></input>
                  {short ? (
                    <p>
                      password is too short it needs to be 8 letters at least
                    </p>
                  ) : null}
                  <label>Confirm your password</label>

                  <input type="password" name="confirm" required></input>
                  {!match ? <p>Password doesn't match</p> : null}
                  <ul>
                    <li>
                      <button className={classes.btn}>Create account</button>
                    </li>
                    <li>
                      {" "}
                      <a
                        href="#"
                        className={classes.loginin}
                        onClick={() => {
                          setLogin(true);
                        }}
                      >
                        Or login instead
                      </a>
                    </li>
                  </ul>
                </form>
              ) : (
                <>
                  <Icon
                    className="arrow"
                    icon="bi:arrow-left-circle"
                    onClick={() => {
                      setLogin(false);
                    }}
                  />
                  <div className={classes.login}>
                    <form onSubmit={submitLogin}>
                      <label>Enter your username</label>
                      <input
                        type="text"
                        name="username"
                        required
                        onChange={valid}
                        maxLength="15"
                      ></input>

                      <label>Enter your password</label>
                      <input type="password" name="password" required></input>
                      {!correct ? (
                        <p>password or username is not correct</p>
                      ) : null}
                      <button className={classes.btn}>Login</button>
                    </form>
                  </div>
                </>
              )}
            </div>
          </div>

          <h1 className={classes.goldry}>Goldry</h1>
          <Footer />
        </>
      ) : (
        <>
          <Mainav />
          <div className={classes.welcome}>
            <h1>{`Welcome! ${
              JSON.parse(localStorage.getItem("user")).username
            }`}</h1>
            <br />
            <h1> you are logged in</h1>
            <span className="iconify" data-icon="bi:check-circle-fill"></span>
          </div>
          {setTimeout(() => (window.location.href = "products"), 2000)}
        </>
      )}
    </>
  );
};
export default SignUp;
