import { useState } from "react";
import classes from "../../Components/Products/Product/Product.module.css";
import { Icon } from "@iconify/react";
import classes2 from "./ShoppingCartItems.module.css";
const ShoppingCartItems = ({ products }) => {
  let totalPrice = 0;

  // const [backgroundImage , setBackgroundImage] =useState({});

  return (
    <div className={classes2.flex}>
      {products.map((v) => {
        let backgroundImage = {
          width: "35rem",
          height: "20rem",
          backgroundImage: `linear-gradient(to bottom, transparent, black) , url(${v.image}) `,
        };

        return (
          <div className={classes.product} style={backgroundImage}>
            <Icon className="x2" icon="bi:x-lg" />
            <p>{v.title}</p>
            <p>{v.price}$</p>
            {(totalPrice += Number(v.price))}
          </div>
        );
      })}
    </div>
  );
};

export default ShoppingCartItems;
