import classes from "./Spinner.module.css";

function Spinner() {
  return (
    <div className={classes.spinnerContainer}>
      <div className={classes.body}>
        <div className={classes.loading}>
          <div className={classes.arc}></div>
          <div className={classes.arc}></div>
          <div className={classes.arc}></div>
        </div>
      </div>
    </div>
  );
}
export default Spinner;
