import classes from "./ShoppingCart.module.css";
import Aux from "../HOC/Auxalliry";
import MainNav from "../Home/Nav/MainNav";
import Spinner from "../Spinner/Spinner";
import { useState, useEffect } from "react";
import { app } from "../../base";
import ShoppingCartItems from "../../DB/Users/ShoppingCartItems";
function ShoppingCart() {
  const db = app.firestore();
  const [items, setItems] = useState(null);
  const [data, setData] = useState(null);
  // const [products, setProducts] = useState([])
  let products = [];
  let username;
  if (localStorage.getItem("user")) {
    username = JSON.parse(localStorage.getItem("user")).username;
  }
  useEffect(() => {
    if (username) {
      const getData = async () => {
        const productsIds = await db.collection("users").doc(username).get();
        setItems(productsIds.data().products);
      };

      getData();
    }
  }, []);
  useEffect(() => {
    const getData = async () => {
      // const temp = items.split(" ").filter((w) => w !== "");

      // let arr = [];

      const getdProducts = await db.collection("products").get();
      setData(
        getdProducts.docs.map((doc) => {
          return doc.data();
        })
      );
    };
    getData();
  }, []);

  if (items) {
    const temp = items.split(" ").filter((w) => w !== "");
    if (data && temp) {
      data.forEach((v, i) => {
        if (temp.includes(v.id)) {
          products.push(v);
        }
      });
    }
  }

  return (
    <Aux>
      {/* {<Spinner />} */}

      <MainNav />

      <div className={classes.body}>
        <div className={classes.container}>
          {!products ? (
            <>
              <p>You have no items added</p>
              <br />
              <span className="iconify" data-icon="mdi:ring"></span>
            </>
          ) : (
            <div className={classes.ProductsContainer}>
              <ShoppingCartItems products={products} />
            </div>
          )}
        </div>
      </div>
    </Aux>
  );
}

export default ShoppingCart;
