import classes from "./MainNav.module.css";
import ShoppingCartIcon from "../../../assets/imgs/ShoppingCart.svg";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import { Icon } from "@iconify/react";
import Modal from "../../Modal/Modal";
function MainNav({ loged }) {
  const [singedIn, setSignedIn] = useState(false);
  const [modal, setModal] = useState(false);
  useEffect(() => {
    const result = localStorage.getItem("user");
    if (result) {
      setSignedIn(JSON.parse(result));
    }
  }, [localStorage.getItem("user")]);

  return (
    <>
      <div className={classes.nav}>
        <ul>
          <li className={classes.logo}>
            <p>Goldry</p>
          </li>

          <li>
            {singedIn ? (
              <Icon
                className="logout"
                icon="ic:outline-logout"
                onClick={() => {
                  setModal(true);
                }}
              />
            ) : null}
          </li>
          <li>
            <Link to="/Shopping-Cart">
              <ion-icon name="cart-outline"></ion-icon>
            </Link>
          </li>
          {!singedIn ? (
            <li>
              <Link to="/Signup">Signup</Link>
            </li>
          ) : null}

          <li>
            <Link to="/Products">Products</Link>
          </li>
          <li>
            <Link to="/">Home</Link>
          </li>
        </ul>
      </div>
      {modal ? <Modal value={{ display: "flex" }} /> : null}
    </>
  );
}
export default MainNav;
